
@import url("https://fonts.googleapis.com/css?family=Lato:300,300i,400,700");
html,body{height:100%
}
body{margin:0 auto;font-size:16px
}
.m__background{position:fixed;content:"";height:100%;width:100%;z-index:-1;height:100%;background-image:url("../assets/background.jpg");background-repeat:no-repeat;background-size:cover;background-position:50% 50%
}
.m__background:after{position:absolute;top:0;content:"";height:100%;width:100%;background-color:rgba(6,214,160,0.9)
}
h1,h2,h3,h4,h5{margin:0
}
ul,ol{margin:0;padding:0
}
.m__landing{width:100%;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;height:calc(100vh - 50px)
}
@media (min-width: 680px){
.m__landing{height:calc(100vh - 60px)
}
}
.m__landing__container{padding:2rem;max-width:100%
}
@media (min-width: 680px){
.m__landing__container{max-width:500px
}
}
.m__landing__heading{font-size:1.5rem;font-family:"Lato",sans-serif;color:#fff;font-weight:300;letter-spacing:0.1rem;font-style:italic;display:block;text-transform:uppercase
}
.m__landing__heading--bold{display:block;font-style:normal;font-size:2rem;font-weight:700
}
.m__landing__copy{font-family:"Lato",sans-serif;color:#fff;font-style:italic;font-size:1rem;margin-top:2rem
}
.m__landing__cta{width:auto;border:solid 1px #fff;padding:0.5rem 1.75rem;text-transform:uppercase;font-family:"Lato",sans-serif;font-size:1rem;color:#fff;font-weight:700;border-radius:5px;margin-top:2rem;background:transparent;transition:background 0.2s ease-out, color 0.2s ease-out
}
.m__landing__cta:hover{cursor:pointer;background:#fff;color:#06d6a0;border:solid 1px transparent
}
.m__landing__login{font-family:"Lato",sans-serif;font-size:0.75rem;letter-spacing:0.1rem;color:#fff;text-transform:uppercase;font-weight:400;position:absolute;top:2rem;right:1rem;background:transparent;border:none
}



